<template>
    <div>
        <div class="jsModal" v-show="autoTarget">
            <div class="c-overlay">
                <div class="c-overlay__contents">
                    <div class="c-overlay__ttl">{{ autoTarget }}</div>
                    <div class="c-overlay__description"><span class="u-red">*</span>KeywordKeyword
                    </div>
                    <div class="c-overlay__db">
                        <table class="c-overlay__db__table">
                            <tbody class="c-overlay__db__tbody">
                                <tr class="c-overlay__db__tr">
                                    <th class="c-overlay__db__th">Keyword</th>

                                    <td class="c-overlay__db__td">{{db_text}}</td>
                                </tr>
                                <tr class="c-overlay__db__tr">
                                    <th class="c-overlay__db__th">Condition</th>

                                    <td class="c-overlay__db__td">{{db_condition}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="c-overlay__btnContainer c-overlay__btnContainer--auto c-overlay__db__border">
                        <div class="c-search">
                            <label class="ef">
                                <input type="text" class="c-search__input" name="keyword" placeholder="Keyword"
                                    v-model="add_keyword" />
                                <input class="c-search__submit" type="submit" value="" v-on:click="addSearchText()" />
                            </label>
                            <div v-if="getCookie()" class="c-search__keywords">
                                <nav class="c-solidMenu">
                                    <ul>
                                        <li v-for="(keyword, i) in getCookie()" :key="i" :id="'like_' + keyword"><a
                                                href="javascript:void(0)"
                                                id="cookiesDom"><span>{{keyword}}</span></a><input
                                                class="c-search__submit" type="submit" value=""
                                                v-on:click="deleteSearchTextCookie(keyword)" /></li>

                                    </ul>
                                </nav>
                            </div>
                        </div>
                        <select v-on:blur="setCookieCondition()" class="c-appBtn" name="like_conditions"
                            id="condition-like-select">
                            <option v-if="!isCookieCondition" selected disabled value="NO"></option>
                            <option v-else selected disabled :value="isCookieCondition">{{ isCookieCondition }}</option>

                            <option v-for="item in conditions" :value="item.value" :key="item.value">
                                {{ item.label }}
                            </option>
                        </select>
                        <button class="c-appBtn" v-on:click="searchAutoLikeSave()"></button>
                        <button v-show="!auto_like_flg" class="c-appBtn" v-on:click="searchAutoLikeStart()">
                            ON</button>
                        <button v-show="auto_like_flg" class="c-appBtn" v-on:click="searchAutoLikeStop()">
                            OFF</button>
                        <button class="c-appBtn" v-on:click="autoCancel()"></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="c-appBtn">
            <a class="c-appBtn--none" :class="{'c-appBtn--auto': auto_like_flg}"
                v-on:click="autoAction('Auto Like Search Keyword')">
                <span v-if="auto_like_flg"></span><span v-else></span>
            </a>
        </div>
    </div>
</template>

<script>
export default {
    props: ['user_id', 'auth_screen_name', 'auto_like_flg', 'db_search_text_condition'],
    data: function() {
        return {
            db_text: this.isDbSearchText(),
            db_condition: this.isDbCondition(),
            autoTarget: '',
            add_keyword: '',
            keywords: '',
            cookiesData: this.getCookie(),
            conditions: [
                {label: "NOT", value: 'NOT'},
                {label: "AND", value: 'AND'},
                {label: "OR", value: 'OR'},
            ],
            isCookieCondition: this.$vueCookies.get('ConditionLike' + this.user_id + this.auth_screen_name),
        }
    },
    methods: {
        isDbSearchText: function () {
            if (this.db_search_text_condition) {
                return this.db_search_text_condition.search_text;
            } else {
                return '';
            }
        },
        isDbCondition: function () {
            if (this.db_search_text_condition) {
                return this.db_search_text_condition.like_condition;
            } else {
                return '';
            }
        },
        setCookieCondition: function () {
            var select = document.getElementById('condition-like-select');

            console.log(select.value)
            this.$vueCookies.config(60 * 60 * 24 * 30, '');
            this.$vueCookies.set('ConditionLike' + this.user_id + this.auth_screen_name, select.value);

            if (this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name)) {
                var cookieData = this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name);
                var arrayCookieData = cookieData.split( ',' );
                if ( select.value == 'NOT') {
                    if (arrayCookieData.length > 1) {
                        alert('NOT Keyword');
                    }
                } else if ( (select.value == 'AND') || (select.value == 'OR')) {
                    if (arrayCookieData.length <= 1) {
                        alert('AND  OR Keyword');
                    }
                }
            }
        },
        autoAction: function(targetName) {
            this.autoTarget = targetName;
        },
        autoCancel: function () {
            this.autoTarget = '';
        },
        addSearchText: function() {
            this.$vueCookies.config(60 * 60 * 24 * 30, '');
            if (this.add_keyword) {
                if (this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name)) {
                    var cookieData = this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name);
                    var arrayCookieData = cookieData.split( ',' );
                
                    // 
                    if (arrayCookieData.includes(this.add_keyword)) {
                        alert(this.add_keyword + '');  // 
                        this.add_keyword = '';
                    } else {
                        arrayCookieData.push(this.add_keyword);
                        this.keywords = arrayCookieData;
                        this.add_keyword = '';
                        this.$vueCookies.set('SearchLikeText' + this.user_id + this.auth_screen_name, arrayCookieData);
                        console.log(this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name));
                    }
                } else {
                    this.keywords = this.add_keyword;
                    this.$vueCookies.set('SearchLikeText' + this.user_id + this.auth_screen_name, this.add_keyword);
                    this.add_keyword = '';
                    console.log(this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name));
                }
            }
        },
        deleteSearchTextCookie: function(keyword) {
            this.$vueCookies.config(60 * 60 * 24 * 30, '');
            var cookieData = this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name);
            var arrayCookieData = cookieData.split( ',' );
            var index = arrayCookieData.indexOf(keyword);
            arrayCookieData.splice(index, 1);
            this.keywords = arrayCookieData;
            this.$vueCookies.set('SearchLikeText' + this.user_id + this.auth_screen_name, arrayCookieData);
            console.log(this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name));
            const keywordDom = document.getElementById('like_' + keyword);
            keywordDom.remove();

            if (this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name) == null) {
                $cookies.remove('SearchLikeText' + this.user_id + this.auth_screen_name);
            }
        },
        getCookie: function() {
            this.$vueCookies.config(60 * 60 * 24 * 30, '');
           if ( this.$vueCookies.isKey('SearchLikeText' + this.user_id + this.auth_screen_name)) {
            var cookieData = this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name);
            var arrayCookieData = cookieData.split( ',' );
            return arrayCookieData;
           } else {
            return ;
           }
        },
        searchAutoLikeSave: function() {
            this.$vueCookies.config(60 * 60 * 24 * 30, '');

            if (this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name)) {
                // Keyword 
                var cookieData = this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name);
                var arrayCookieData = cookieData.split(',');
                var cookieCondition = this.$vueCookies.get('ConditionLike' + this.user_id + this.auth_screen_name);

                if (arrayCookieData.length > 1 && this.$vueCookies.get('ConditionLike' + this.user_id + this.auth_screen_name) == 'NOT') {
                    alert('NOT Keyword');
                } else if (arrayCookieData.length <= 1 && this.$vueCookies.get('ConditionLike' + this.user_id + this.auth_screen_name) == ('AND' || 'OR')) {
                    if (arrayCookieData.length <= 1) {
                        alert('AND  OR Keyword');
                    }
                } else {
                    const formData = new FormData();
                    formData.append('user_id', this.user_id);
                    formData.append('screen_name', this.auth_screen_name);
                    formData.append('array_search_text', arrayCookieData);
                    formData.append('condition', cookieCondition);

                    this.$axios.post('/api/twitter/autoLikeSave', formData)
                        .then((res) => {
                            this.add_flg = true;
                            console.log(res)
                            this.db_text = cookieData;
                            this.db_condition = cookieCondition;
                            alert('Keyword')
                        })
                        .catch((error) => {
                            console.log('searchAutoLikeSave')
                            console.log(error)
                        })
                }   

            } else {
                // Keyword 
                alert('Keyword1');
            }
        },
        searchAutoLikeStart: function () {
            if (this.$vueCookies.get('SearchLikeText' + this.user_id + this.auth_screen_name)) {
                const formData = new FormData();
                formData.append('user_id', this.user_id);
                formData.append('screen_name', this.auth_screen_name);

                this.$axios.post('/api/twitter/autoLikeStart', formData)
                    .then((res) => {
                        console.log(res)
                        window.location.reload(false)
                    })
                    .catch((error) => {
                        console.log('searchAutoLikeStart')
                        console.log(error)
                    })
            } else {
                alert('Keyword1');
            }
        },
        searchAutoLikeStop: function() {
            const formData = new FormData();
            formData.append('user_id', this.user_id);
            formData.append('screen_name', this.auth_screen_name);

            this.$axios.post('/api/twitter/autoLikeStop', formData)
                .then((res) => {
                    console.log(res)
                    window.location.reload(false)
                })
                .catch((error) => {
                    console.log('searchAutoLikeStop')
                    console.log(error)
                })
        },
    },
}
</script>
